/* Funcin para mostrar cuadro de error si el usuario no tiene cuenta */
function MyVerticallyCenteredModal(props) {
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    Debes iniciar sesin para aadir un comentario!
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <h4>Inicia sesin en tu cuenta o crea una nueva para aadir comentarios</h4>
                <div>
                    <h5>
                    <Link to="/CrearCuenta">
                    <Button className="registro-cancion" >Registrarse</Button>
                    </Link>
                    </h5>
                    <h5>
                    <Link to="/InicioSesion">
                    <Button className="inicioSesion-cancion" >Iniciar Sesion</Button>
                    </Link>  
                    </h5>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={props.onHide}>Cerrar</Button>
            </Modal.Footer>
        </Modal>
    );
}

MyVerticallyCenteredModal.propTypes = {
    onHide: PropTypes.func.isRequired, // Validacin de tipo para la prop onHide
};

