import React, { useEffect, useState } from "react";
import BookmarkAddIcon from '@mui/icons-material/BookmarkAdd';
import BookmarkAddedIcon from '@mui/icons-material/BookmarkAdded';
import axios from 'axios'
import { useParams } from "react-router-dom";
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import PropTypes from 'prop-types';

import "../styles/detallesPeliculasSeries-style.css";

//<-------------------------------------------------------------------------------------------------------->//
//<-------------FUNCIN PARA MOSTRAR CUADRO DE ERROR SI EL USARIO NO TIENE CUENTA-------------------------->//
//<-------------------------------------------------------------------------------------------------------->//

/* Funcin para mostrar cuadro de error si el usuario no tiene cuenta */
function MyVerticallyCenteredModal(props) {
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    Debes iniciar sesin para {`${props.error}`}!
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <h4>Inicia sesin en tu cuenta o crea una nueva para {`${props.error}`}</h4>
                <div style={{ textAlign: "center" }}>
                    <h5>
                        <Link to="/CrearCuenta">
                            <Button className="registro-cancion" >Registrarse</Button>
                        </Link>
                    </h5>
                    <h5>
                        <Link to="/InicioSesion">
                            <Button className="inicioSesion-cancion" >Iniciar Sesion</Button>
                        </Link>
                    </h5>
                </div>
            </Modal.Body>

        </Modal>
    );
}

//<-------------------------------------------------------------------------------------------------------->//

function DetallesPeliculas() {
    const { id } = useParams();
    const [movie, setMovie] = useState(null);
    const language = 'es-ES';
    const [similarMovies, setSimilarMovies] = useState([]);

    const [modalShowComentarios, setModalShowComentarios] = React.useState(false);
    const [modalShowGuardar, setModalShowGuardar] = React.useState(false);

//<-------------------------------------------------------------------------------------------------------->//
//<------------------------------FUNCIONES Y CONSTANTES PARA COMENTARIOS----------------------------------->//
//<-------------------------------------------------------------------------------------------------------->//

    //const [comments, setComments] = useState([]);

    // Autores de comentarios
    const autores = [
        "John Doe", "Alice Smith", "Michael Johnson", "Emily Brown", "David Lee",
        "Emma Garcia", "Daniel Martinez", "Olivia Wilson", "James Taylor", "Sophia Anderson",
        "William Clark", "Ava Rodriguez", "Benjamin Hernandez", "Mia Moore", "Jacob Perez",
        "Charlotte Davis", "Alexander Gonzalez", "Amelia Martinez", "Ethan Wilson", "Harper Lopez",
        "Liam Thompson", "Isabella Carter", "Christopher Thomas", "Sophia Baker", "Ryan Reed",
        "Madison Young", "Elijah Scott", "Scarlett Evans", "Nathan Morris", "Grace Turner"
    ];

    // Comentarios
    const comentarios = [
        "Una experiencia cinematogrfica asombrosa! Esta pelcula me dej sin aliento de principio a fin.",
        "Una obra maestra del cine. La direccin, la actuacin y la narrativa son impecables.",
        "Una pelcula que te hace reflexionar sobre la vida y el propsito. Absolutamente conmovedora!",
        "Una joya oculta que merece ms reconocimiento. No puedo creer que no la haya visto antes!",
        "Qu increble viaje emocional! Esta pelcula me hizo rer, llorar y reflexionar sobre la vida.",
        "Una mezcla perfecta de accin y drama. Nunca un momento aburrido.",
        "La qumica entre los personajes es palpable. Te sumerges por completo en su mundo.",
        "Una pelcula que te deja en suspenso hasta el ltimo segundo. No puedo esperar a ver la secuela!",
        "Un clsico moderno que nunca pasar de moda. Una verdadera obra de arte!",
        "Los efectos visuales son impresionantes. Te transportan a otro universo por completo.",
        "Una pelcula que desafa las convenciones y rompe barreras. Definitivamente una de mis favoritas!",
        "La banda sonora es simplemente pica. Aade una capa adicional de emocin a cada escena.",
        "Los giros argumentales te mantienen en vilo. Nunca sabes qu esperar a continuacin!",
        "Una pelcula que te hace cuestionar tus propias creencias y percepciones. Muy poderosa!",
        "Los personajes son tan realistas que sientes que podran ser tus amigos. Una pelcula verdaderamente cautivadora!",
        "Una montaa rusa de emociones. Te lleva en un viaje inolvidable!",
        "La cinematografa es impresionante. Cada cuadro es una obra de arte en s mismo.",
        "Una pelcula que te deja reflexionando mucho despus de que terminen los crditos. Absolutamente inolvidable!",
        "Los dilogos son tan afilados que te mantienen pegado a la pantalla. Una delicia para los amantes del cine!",
        "Una pelcula que te hace apreciar la belleza de las pequeas cosas de la vida. Muy conmovedora!",
        "La direccin es magistral. Cada escena est cuidadosamente elaborada para transmitir una emocin nica.",
        "Una historia que se queda contigo mucho despus de que termina. Realmente te hace reflexionar sobre tu propia vida!",
        "La qumica entre el elenco es palpable. Te hacen creer en la historia que estn contando.",
        "Los efectos especiales son tan realistas que te sumergen por completo en el mundo de la pelcula.",
        "Una pelcula que desafa tus expectativas y te deja con ganas de ms. Una verdadera obra maestra del cine contemporneo!",
        "Los temas tratados en esta pelcula son tan relevantes para la sociedad actual. Realmente te hacen pensar!",
        "Una pelcula que te hace rer, llorar y emocionarte. Una montaa rusa de emociones!",
        "La actuacin es simplemente sobresaliente. Cada actor trae algo nico al personaje que interpreta.",
        "Una pelcula que te deja con un sentimiento de esperanza y optimismo. Una verdadera joya del cine!"
    ];

    // Estado para almacenar los comentarios aleatorios actuales
    const [comentariosAleatorios, setComentariosAleatorios] = useState([]);

    // Estado para almacenar el comentario del usuario
    const [userComment, setUserComment] = useState("");

    // Funcin para obtener un elemento aleatorio de un array
    const obtenerElementoAleatorio = (array) => {
        const indiceAleatorio = Math.floor(Math.random() * array.length);
        return array[indiceAleatorio];
    };

    // Funcin para generar 5 comentarios aleatorios y nicos
    const generarComentariosAleatorios = () => {
        const comentariosAleatorios = [];
        while (comentariosAleatorios.length < 4) {
        const comentario = obtenerElementoAleatorio(comentarios);
        const autor = obtenerElementoAleatorio(autores);
        if (!comentariosAleatorios.some((com) => com.texto === comentario)) {
            comentariosAleatorios.push({ texto: comentario, autor });
        }
        }
        setComentariosAleatorios(comentariosAleatorios);
    };

    // Funcin para manejar el envo del comentario del usuario
    const handleUserCommentSubmit = () => {
        if (userComment.trim() !== "") {
        const nuevoComentario = { texto: userComment, autor: localStorage.getItem('username') };
        setComentariosAleatorios((prevComments) => [...prevComments, nuevoComentario]);
        setUserComment(""); // Limpiar el cuadro de texto despus de enviar el comentario
        }
    };

    // Funcin para manejar el cambio en el cuadro de texto del comentario del usuario
    const handleUserCommentChange = (event) => {
        setUserComment(event.target.value); // Actualiza el comentario del usuario
    };

    // Obtener el nombre del usuario
    const usuario = localStorage.getItem('username');

//<-------------------------------------------------------------------------------------------------------->//

//<-------------------------------------------------------------------------------------------------------->//
//<---------------------------------FUNCIONES Y CONSTANTES PARA PELCULAS---------------------------------->//
//<-------------------------------------------------------------------------------------------------------->//

    //Peliculas guardadas en biblioteca
    const [moviesSaved, setMoviesSaved] = useState(() => {
        const savedMovies = localStorage.getItem('moviesSaved');
        return savedMovies ? JSON.parse(savedMovies) : [];
    });

    const handleMovieSave = () => {
        if (moviesSaved.includes(movie.id)) {
            setMoviesSaved(moviesSaved.filter(movieId => movieId !== movie.id));
        } else {
            setMoviesSaved([...moviesSaved, movie.id]);
        }
    };

    useEffect(() => {
        const fetchMovie = async () => {
            const { data } = await axios.get(
                `https://api.themoviedb.org/3/movie/${id}?api_key=fd04580a5174281296d7de8867bc1fa0&language=${language}`
            );
            setMovie(data);
        };

        const fetchSimilarMovies = async () => {
            const response = await axios.get(`https://api.themoviedb.org/3/movie/${id}/similar?api_key=fd04580a5174281296d7de8867bc1fa0&language=~${language}`);
            setSimilarMovies(response.data.results);
        };

        const savedMovies = JSON.parse(localStorage.getItem('moviesSaved'));
        if (savedMovies) {
            setMoviesSaved(savedMovies);
        }
        
        fetchMovie();
        fetchSimilarMovies();
        setComentariosAleatorios([]);
    }, [id]);

    useEffect(() => {
        localStorage.setItem('moviesSaved', JSON.stringify(moviesSaved));
        console.log(moviesSaved);
    }, [moviesSaved]);

    useEffect(() => {
        // Esta funcin se llamar directamente despus de abrir la pgina
        generarComentariosAleatorios();
    }, []);


    if (!movie) {
        return <div>Cargando...</div>
    }

    const fullStars = Math.floor(movie.vote_average / 2);
    const halfStar = movie.vote_average % 2 === 0 ? 0 : 1;
    const emptyStars = 5 - fullStars - halfStar;

//<-------------------------------------------------------------------------------------------------------->//

    return (
        <div id="detallesPeliculas">
            <div className="mx-auto px-5 py-5 d-flex align-items-start">
                <div>
                    <img src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`} alt={movie.title} style={{ height: '400px', width: 'auto' }} />
                </div>
                <div className="mx-auto px-5 py-3">
                    <div style={{ display: 'flex', alignItems: 'center' }}>
                        <h3>{movie.title}</h3>
                        {usuario ? (
                            <button onClick={handleMovieSave} style={{ border: 'none', background: 'transparent' }}>
                            {moviesSaved.includes(movie.id) ? 
                                <div title="Guardada">
                                <BookmarkAddedIcon className="BookmarkaddedIcon" alt={"Guardado"} style={{ marginLeft: '10px', color: 'black' }} />
                              </div>
                              :
                              <div title="Guardar">
                                <BookmarkAddIcon className="BookmarkaddIcon" alt={"Guardar"} style={{ marginLeft: '10px', color: 'black' }} />
                              </div>}
                            </button>
                        ) : (
                            <>
                                <button onClick={() => setModalShowGuardar(true)} style={{ border: 'none', background: 'transparent' }}>
                                <BookmarkAddIcon 
                                    className="BookmarkIcon" 
                                    alt={"Guardar"} 
                                    style={{ marginLeft: '10px', color: 'black' }} 
                                />
                                </button>
                                <MyVerticallyCenteredModal
                                    show={modalShowGuardar}
                                    onHide={() => setModalShowGuardar(false)}
                                    error='guardar pelculas'
                                />
                            </>
                        )}
                    </div>
                    <div>
                        {''.repeat(fullStars)}
                        {''.repeat(halfStar)}
                        {''.repeat(emptyStars)}
                        <span style={{ color: 'black' }}> {movie.vote_average}</span>
                    </div>
                    <div className="py-4">
                        <p style={{ fontSize: '20px' }}>{movie.overview}</p>
                    </div>
                </div>
                <div>
                    <h3 className="mx-auto py-3" style={{ textAlign: 'center' }}>Pelculas similares</h3>
                    <div className="mx-auto px-5 py-3" style={{ display: 'flex', flexWrap: 'wrap', width: '300px' }}>
                        {similarMovies.slice(0, 4).map(similarMovie => (
                            <div key={similarMovie.id} style={{ width: '50%', padding: '10px' }}>
                                <Link to={`/detallesPeliculas/${similarMovie.id}`}>
                                    <img src={`https://image.tmdb.org/t/p/w500${similarMovie.poster_path}`} alt={similarMovie.title} style={{ width: '100%', height: 'auto' }} />
                                </Link>
                            </div>
                        ))}
                    </div>

                </div>
            </div>

{/*<---------------------------------COMENTARIOS------------------------------------------------------->*/}

            <div className="container">
                <div className="row">
                    <div className="col-md-6">
                        <h4 className="CommentTitle">Comentarios:</h4>
                        <div className="tabla">
                            {comentariosAleatorios.map((comment, index) => (
                                <div key={index} className="CommentBox">
                                <p><strong>{comment.autor}</strong>: {comment.texto}</p>
                                </div>
                            ))}
                        </div>
                        <Button variant="contained" color="secondary" className="NextCommentButton" style={{ backgroundColor: 'purple', color: 'white' }} onClick={generarComentariosAleatorios}>
                        Ver ms comentarios
                        </Button>
                    </div>
                    <div className="col-md-2"></div>
                    <div className="col-md-4">
                        <div className="UserInputContainer">
                            {/* Cuadro de texto para la opinin del usuario */}
                            <textarea
                            placeholder="Escribe tu opinin aqu..."
                            value={userComment}
                            onChange={handleUserCommentChange}
                            className="UserOpinion"
                            style={{ width: '100%', height: '100px' }}
                            />
                            {/* Botn para aadir comentario */}
                            
                        </div>
                        {usuario ? (
                            <>
                            <Button variant="contained" color="primary" className="CommentButton" style={{ backgroundColor: 'blue', color: 'white' }} onClick={handleUserCommentSubmit}>
                                Aadir comentario
                            </Button>
                            </>
                        ) : (
                            <>
                            <Button variant="contained" color="primary" className="CommentButton" style={{ backgroundColor: 'blue', color: 'white' }} onClick={() => setModalShowComentarios(true)}>
                                Aadir comentario
                            </Button>
                            <MyVerticallyCenteredModal
                                show={modalShowComentarios}
                                onHide={() => setModalShowComentarios(false)}
                                error='aadir comentarios'
                            />
                        </>
                        )}
                    </div>
                </div>
            </div>
{/*<----------------------------------------------------------------------------------------------->*/}
        </div>
    )
}

// Define la validacin de tipos para la prop 'error'
MyVerticallyCenteredModal.propTypes = {
    error: PropTypes.string.isRequired // Suponiendo que 'error' es una cadena requerida
};


export default DetallesPeliculas;